body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.tweetBox{
    display:flex;
    flex-direction: column;
    width: 40%;
    height: 250px;
    margin: auto;
    background-color: #E6E6E6;
    border-radius: 30px;
    margin-top: 20px;
}

.textBox{
    flex: 1 1;
    align-self: center;
    text-align: left;
    margin-top: 30px;
    margin-bottom: 10px;
    padding-left: 20px;
    resize: none;
    background-color: #E6E6E6;
    border: none;
    outline: none;
    width: 100%;
}

.tweetBtn {
    display: flex;
    width: 100px;
    height: 40px;    
    align-self: right;
    text-align: center;
    background-color: #1DA1F1;
    border: none;
    border-radius: 20px;
}
.tweetFeed{
    display:flex;
    flex-direction: column;
    width: 40%;
    height: 250px;
    margin: auto;
    background-color: #E6E6E6;
    border-radius: 30px;
    margin-top: 20px;
}
.header{
    display: flex;
    justify-content: space-between;
    padding: 20px;
}
.tweet{
    display: flex;
    justify-content: left;
    padding-left: 20px;
}
.followBtn{
    display: flex;
    width: 100px;
    height: 40px;    
    align-self: right;
    text-align: center;
    background-color: #1DA1F1;
    border: none;
    border-radius: 20px;
}
